.no-webp .inicio{
    background-image: url(../img/bannerPrincipal.jpg);
    // background-image: url(../img/header.jpg);
}
.webp .inicio{
    background-image: url(../img/bannerPrincipal.jpg);
    // background-image: url(../img/header.webp);
}
.header{
    background-color: $azulCielo;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;
        @include tablet{
            min-height: 70rem;
        }
        .contenido-header{
            @include tablet{
                min-height: 70rem;
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1{
        text-align: left;
        color: $blanco;
        font-weight: $black;
        line-height: 2;
        max-width: 60rem;
        font-size: 5rem;
    }
}

.img-logoGorsa{
    width: 100%;
    // margin-bottom: 2rem;
    @include tablet{
        // width: 55%;
        width: 23%;
    }
}